
// I think it could be optimized by pushing deltas.
export function UndoStack(initState) {
	const stack = [initState];
	let index = 0;
	return {
        
		push(newState) {
			if (index < stack.length - 1) {
				stack.splice(index + 1, stack.length - (index + 1));
			}
			stack.push(newState);
			index = stack.length - 1;
		},
        
		undo() {
			if (index > 0) {
				index -= 1;
			}
			return stack[index];
		},
        
		redo() {
			if (index < stack.length - 1) {
				index += 1;
			}
			return stack[index];
		},
        
		_stack() {
			return stack;
		}
	};
}