<script lang="ts">
	import * as hljs from 'highlight.js';
	// The default import imports all languages.
	// Therefore it is likely to be more efficient to import only the library and the languages you need:
	// import hljs from 'highlight.js/lib/core';
	// import javascript from 'highlight.js/lib/languages/javascript';
	// import html from 'highlight.js/lib/languages/html';
	// import css from 'highlight.js/lib/languages/css';
	// hljs.registerLanguage('javascript', javascript);
	// hljs.registerLanguage('html', html);
	// hljs.registerLanguage('css', css);

	// https://highlightjs.readthedocs.io/en/latest/api.html#configure-options
	hljs.configure({
		tabReplace: '  ', // 2 spaces
						// … other options aren't changed

	});

	export let src;
	export let name;
	export let lang;
	let error;
	let code;
	let self;

	fetch(src)
	.then(response => response.text())
	.then(text => {
		code = text;
		// Set Name
		if (name === undefined) {
			const split = src.split('/');
			name = split[split.length - 1];
		}
		// Set Language
		if (!lang) {
			const split = src.split('/');
			const ext = split[split.length-1].split('.')[1];
			if (ext) {
				lang = ext;
			}
		}

		// Highlight Code Block
		setTimeout(() => hljs.highlightBlock(self), 0)
	})
</script>

<div dir="ltr" class="snippet">
	<div class="snippet-name">
		{name}
	</div>
	<pre>
		<code class="lang-{lang}" bind:this={self}>
			{code}
		</code>
	</pre>
	{#if !code}
		<div>waiting <b>{src}</b> to be fetched</div>
	{:else if error}
		<div class="error">
			{error.message}
		</div>
	{/if}
</div>


<style>
	code {
		font-family: 'Consolas', Courier, monospace;
	}
	pre {
		margin: 0;
	}
	.snippet {
		border: 1px #cccccc solid;
		margin: 16px auto;
	}
	.snippet-name {
		padding: 4px;
		/* text-align: center; */
		background-color: var(--label-bg);
	}
	.error {
		color: red;
}
</style>