<script>
	export let src;
	export let caption;
	export let showCaption = true;
	// export let align;
	export let zoom = false;
	let self;

	// Detect if it should be zoomable
	// By measuring the width and the height
	$: if (src && self) {
		if (self.naturalWidth > 1.2 * self.width
		|| self.naturalHeight > 1.2 * self.height) {
			zoom = true;
		}
	}
</script>

<figure class:zoom>
	<img
		bind:this={self}
		src="{src}"
		alt="{caption}"
	/>
	{#if showCaption}
		<figcaption>
			{#if caption.startsWith('http')}
			<a dir="ltr" href="{caption}">{caption}</a>
			{:else}
				{caption}
			{/if}
		</figcaption>
	{/if}
</figure>

<style>
	figure {
		border: 1px #cccccc solid;
		padding: 4px;
		margin: auto;
		max-width: 100%;
		text-align: center;
		box-sizing: content-box;
	}
  
	figcaption {
		padding: 2px;
		text-align: center;
		background-color: var(--label-bg);
	}

	img {
		max-width: 100%;
	}
</style>