	<section>
		<p>علوم الحاسوب تُعنى بإيجاد حلول كُلّيَّة خوارزمية للمسائل؛ فالحاسوب ما هو إلا أداة تنفيذ خوارزميات ، والخوارزميات ما هي إلا خطوات المعالجة المؤدية للحل. ونقصد بالحل الكلي: المُجرَّد عن أعيان المسألة. فيمكن أن نقول أن المبحث الأول في علوم الحاسوب هو الخوارزميات.</p>
		<p>وأي خوارزمية يستطيعها الحاسوب، فإنَّ أي حاسوبٍ بشري يستطيعها -إذا أطلقنا الوقت والجهد- بورقة وقلم، دون تفكير -لو اعتبرنا أنه لا يُخطئ في تتبع الخطوات-.</p>
		<p>أما مستوى إجمال الخُطوة وتفصيلُها فيرجع إلى لغة البرمجة، فكل خوارزمية عبارة عن خوارزمياتٍ جزئية، وهكذا يستمر التضمين حتى نصل إلى العمليات الأوَّلية المنطقية للمعالجة الرقمية، ولُغة البرمجة إذا عَلَت (تجرَّدَت عن التفاصيل)، صارت الخوارزميات أبسط، لكنها قد تسلِبُك الدِّقَّة عند بعض التفاصيل.</p>
		<p>وغالبًا ما تُفكك المسألة لتصبِحَ مسائل، لكلٍّ منها خوارزمية، ثم يركَّبُ الحلُّ فتتكون خوارزمية جامعة.</p>
		<p>ولغة البرمجة تتضمن مفرداتٍ للتحكم في سير تنفيذ الخطوات: كالعبارة الشرطية (لسلوك مسار بحسبِ الشرط)، وعبارة التكرار (للمعالجات المتكررة)، وهي في الأصل دون تحكُّم تسير في التنفيذ بالترتيب من الأعلى للأسفل.</p>
		<p>والمبحث المحوري الثاني: المعلومات، التي تتكون من بيانات؛ يُبحثُ تمثيلها والعمليات عليها، وهو مجالٌ واسعٌ؛ إذ لكلِّ نوعٍ من المعلومات خصائصه التي يُمكن بحثُها لتمثيلها في البيئة الرقمية.</p>
		<p>ففي تمثيل الملفات: الصُّوَر والمقاطع المرئية والمسموعة والكُتُب وغيرها، كلٌّ له عدة صيَغٍ في تمثيله رقميًّا، وهي تتفاوَت في الدقة (الجوْدة) والحجم، ومعايير أخرى= بحسب تمثيلها.</p>
	</section>
		
	<section>
		<h2>أنواع المسائل في علوم الحاسوب</h2>
		<h3>مسألة قرار</h3>
		<p>يكون الجواب فيها لكل مُدخَل بنعم أو لا؛ فمثلاً: هل العدد س أوَّلي، أو هل س أكبر من ص، أو هل النص صحيح نحويًّا، أو هل توجد سيارة في الصورة.</p>
		<h3>مسألة بحث</h3>
		<p>يبحث الحلَّ في فضاء الاحتمالات؛ وهو يتضمَّن القرار، فمثلاً: البحث عن طريق سالك من نقطة إلى أخرى، أو البحث عن الجذر التربيعي لعدد.</p>
		<h3>مسألة عد</h3>
		<p>وتتضمن عدة مسائل بحث؛ مثلاً: عدد الإعرابات الصحيحة للجُملة، أو عدد الطُّرُق السالكة من نقطة إلى أخرى.</p>
		<h3>مسألة تفضيل</h3>
		<p>وهي مسألة بحثٍ وقرار؛ فهي تقريرُ أفضل حلٍّ ضمن حلولٍ صحيحة؛
		مثال1: أفضل مسار رحلة</p>
		<ol>
			<li>بحث الطرق السالكة من نقطة إلى أخرى</li>
			<li>تقرير أفضل طريق من بينها
			مثال2: أفضل خُطَّة للفوز</li>
			<li>بحث الخُطط المُمكنة بعُمق 10 خطوات (في لعبة الشطرنج مثلاً)</li>
			<li>تقرير أفضل الخُطط من بينها
			مثال3: أفضل جدوَل</li>
			<li>بحث كل الجداول التي تحقق شروطًا مُحددة (مثل غير متعارضة، وتكون في الصباح)</li>
			<li>تقرير أفضل جدوَل من بينها</li>
		</ol>
	</section>

	
	<section>
		<h2>التعقيد الخوارزمي</h2>
		<p>جوانب تعقيد الخوارزمية في الغالب جانبان: عدد العمليات ومساحة التخزين؛ فبعض الخوارزميات أفضلُ من غيرها لأنها تصل للحل بعدد عمليات أقل ولو تطلبت مساحة أكبر، وبعضها أفضل من الجانبين، وهذا يُدرَسُ في مجال تحليل وتصميم الخوارزميات «Design and Analysis of Algorithms».</p>
		<h3>المسألة المستعصية</h3>
		<p>المسألة المستعصية  هي ما لا يوجد لها حلٌّ مُطابقٌ إلا ويتفاقَمُ تعقيداً مع حجم المسألة، ونقصد بالتعقيد عدد الخطوات أو مساحة التخزين المطلوبة،  فإذا كانت مسألة تفضيل مستعصية؛ فإن القرار فيها يستعصي وقد لا يستعصي البحث. انظر مسألة: مندوب المبيعات المُسافِر «The Travelling Salesperson Problem».
		وبعضُ المسائل المستعصية يُمكن فحصُ صِحَّة حلها وإن لم يُمكن حلُّها عمليًّا.
		انظر أيضاً: نظرية قابلية الحوْسَبة «Computability Theory».</p>
		<h3>الحل التقريبي</h3>
		<p>الوسيلة لحل بعض المسائل المستعصية هو الاقتراب من الحل؛ والمثال المشهور الذي يُعاني منه الجميع: مسألة جدوَلة الحصص (المدرسية)، فالجدوَل الذي يقرره الحاسوب هو حلٌّ تقريبي؛ وذلك لأنه لا يستطيع أن يعتبر كُل فضاء الحل ويُقرر.
		وفي الغالب فإنه كُلَّ ما كان تكوينُ الخُطَّة في كُلِّ خطوة مؤثِّراً (ولو احتمالاً) على عدد أكبَر من المتغيِّرات صار لازمًا اعتبارُها ومُعالجتها، فيعسُر بقدر ذلك إيجادُ حلٍّ في وقتٍ عمليّ؛ فيُلجأ لطُرُقٍ تقريبية.</p>
	</section>

	<section>
		<h2>بعض مجالاته</h2>
		<p>موضوع علم الحاسوب: الحوسَبة والمعلومات؛ فنجد نظرية الحوسَبة تبحث ما يُمكن حِسابُه وما لا يُمكن حسابه، ومجال تحليل وتصميم الخوارزميات يبحث ما به يُفاضَلُ بينها بحسْبِ الاعتبار، والمعلوماتُ مبحثٌ أساسيٌّ: تمثيلُها وانتقالُها وحِفظُها والبحث عنها وفيها واختصارُها واسترجاعُها، وذلك مُتعلّقٌ بمجال هيكَلة البيانات وقواعد البيانات وأمن المعلومات والتشفير وغيره من علوم الحاسوب.
		واستخدم أقوامٌ الحاسوب أداةً لعلومهم؛ فظهر علم الفيزياء الحسابي، وعلم الكيمياء الحسابي، ومثله في الأحياء واللُّغات وغير ذلك كثير؛ إذْ تُحاكى الأنظِمَة في هذه العلوم بتمثيل المعلومات في الحاسوب وتعريف علاقاتِ المُتغيّرات، فينشأ نِظامٌ اصطناعيٌّ نموذجي يُمَكِّنُ الباحثين من تعديل مُتغيِّراتٍ ورؤيةِ أثر ذلك؛ وذلك يُوفِّر تكاليف التجاربِ الحيَّة حين يكون ذلك مُمكِنًا والنموذَجُ ضِمْنَ الدقة المطلوبة.</p>
		<p>وعلى أبوابِ المُستقبل علمُ الحوْسَبة الكميَّة الذي هو نقلة نوعيَّةٌ في علوم الحاسوب لكن قالوا لن يكون أثره في مجال تقنية المعلوماتِ أوَّلاً وقد لا يكونُ آخرًا، بل مجالاتُه الأولى ستكون في الكيمياء والأمور الدقيقة.</p>
		<p>وتطوَّرت أجهزة الحاسوب فتضاعفت قُدرة المُعالَجة أضعافًا مع تضاعُفِ التخزين إلى جانِب نمُوِّ عدد البيانات التي تُجمَعُ وتتداول على الشبكة= فأصبح مُمكِنًا تطبيق نظريات الذكاء الاصطناعي والتي من أهمِّها نظرية العصبونات الاصطناعية، فصار التعرُّف على الأنماطِ المُعقَّدة مُمكنًا؛ فتعرَّف على الوجوه والأصوات والكلام، وصار ينطقُ ما يُكتبُ له، ويجِدُ جوابًا لما يُسألُ عنه؛ ولا يزال البحث والتطوير في الذكاء الاصطناعي مُستمرًّا.</p>
		<p>ومهندسو الحاسوب يبحثون في تصميمها وتوصيلِها وتفاصيل ذلك، وهُم يتعاملون مع الجُزءِ الماديّ له وكذلك البرمجيّ.</p>
		<p>أما هندسة البرمجيات فمجالٌ يُعامِل البرامِج المُركَّبة كمشاريعَ كبيرة -وهي كذلك-، ومجالُهم التطبيقات والمواقِعُ والخوادِمُ وما إلى ذلك. وقيل: هندسة البرمجيات هي حاصل إضافة عامِلَ الوقت إلى البرمجة.</p>
		<p>وهُناك علومٌ وأمورٌ متعلقة وتطبيقاتٌ لم أتطرق إليها وهي كثيرة، لكن لعل في ما ذُكر كفاية في تصوُّرِ علوم الحاسوب ومجالاتها العامَّة وموضوعِها الأساسي وثمرات ذلك.</p>
	</section>
