<script lang="ts">
    export let open = false;
    export let x = 0;
    export let y = 0;

    let menu: HTMLElement;

    $: if (open) {
        if (menu) {
            menu.style.left = `${x}px`;
            menu.style.top = `${y}px`;
            menu.style.display = "block";
        }
    } else {
        if (menu) {
            menu.style.display = "none";
        }
    }
</script>

<div class="menu" bind:this="{menu}" on:click>
    <div class="menu-options">
        <slot />
    </div>
</div>

<style lang="scss">
    .menu {
        width: 120px;
        box-shadow: 0 4px 5px 3px rgba(0, 0, 0, 0.2);
        background: #fff;
        position: absolute;
        display: none;
        z-index: 10;

        .menu-options {
            list-style: none;
            padding: 10px 0;
        }

        /* https://stackoverflow.com/questions/46086483/how-to-apply-styles-to-slot-element-in-svelte */
        .menu-options > :global(*) {
            font-weight: 500;
            font-size: 14px;
            padding: 10px 40px 10px 20px;
            cursor: pointer;
        }
        .menu-options > :global(*):hover {
            background: rgba(0, 0, 0, 0.2);
        }
    }
</style>
