<script>
    export let celsius = 0;
    let fahrenheit = C_to_F(celsius);

    const handleChange = (unit) => (evt) => {
        const value = Number(evt.target.value);
        
        if (isNaN(value)) {
            return;
        }

        switch(unit) {
            case 'F': {
                celsius = F_to_C(value);
            } break;

            case 'C': {
                fahrenheit = C_to_F(value);
            } break;

            default: {
                console.log('unknown temperature unit:', unit);
            }
        }
    };

    function C_to_F(c) {
        return c * (9/5) + 32;
    }
    function F_to_C(f) {
        return (f - 32) * (5/9);
    }
</script>


<div class="container">
    
    <!-- <div class="field"> -->
        <input type="number" value={celsius} on:input={handleChange('C')} id="celsius">
        <label for="celsius">Celsius</label>
    <!-- </div> -->

    <!-- <div class="field"> -->
        <input type="number" value={fahrenheit} on:input={handleChange('F')} id="fahrenheit">
        <label for="fahrenheit">Fahrenheit</label>
    <!-- </div> -->

</div>

