<script>
    const ms = 100; // update rate, the less the quicker.

    let elapsedTime = 10 * 1000;
    let d = 30;
    $: duration = d * 1000;

    const handleReset = () => {
        elapsedTime = 0;
    };

    const newTimer = () => {
        return setInterval(() => {
            elapsedTime += ms;
        }, ms)
    };

    let interval = newTimer();
    
    $: if (elapsedTime >= duration) {
        clearInterval(interval);
        interval = null;
    };

    $: if (interval === null && elapsedTime < duration) {
        interval = newTimer();
    };

    
</script>


<div class="container">
    
    <div>
        <label for="time">Elapsed Time:</label>
        <progress id="time" value={elapsedTime} max={duration}>{elapsedTime}%</progress>
        <span>{elapsedTime / 1000}s</span>
    </div>


    <div>
        <label for="duration">Duration:</label>
        <input type="range" id="duration" min="0" max="90" class="slider" bind:value={d}>
        <span>{duration / 1000}s</span>
    </div>

    <div>
        <button on:click={handleReset}>Reset</button>
    </div>

</div>

<style>
    .container {
        width: 320px;
        margin: 0 auto;
        text-align: left;
    }

    button {
        width: 100%;
        margin-top: 14px;
    }

    .slider {
        -webkit-appearance: none;
        /* width: 100%; */
        height: 8px;
        background: #efefef;
        outline: none;
        opacity: 0.7;
        -webkit-transition: .2s;
        transition: opacity .2s;
        margin: 0;
        padding: 0;
        vertical-align: middle;
        border: none;
    }

    .slider:hover {
        opacity: 1;
    }

    .slider::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        width: 16px;
        height: 16px;
        background: #0075ff;
        border-radius: 100%;
        cursor: pointer;
    }

    .slider::-moz-range-thumb {
        width: 16px;
        height: 16px;
        background: #0075ff;
        border-radius: 100%;
        cursor: pointer;
    }
</style>