<script>
	import Item from './Item.svelte';
	export let ident;
	export let value;
	export let children;
	export let focus;
	export let level;

	let self;
	$: if (focus) {
		self.focus();
		console.log('focus:', self);
	}
</script>


<div class="container">
	<span>{level}</span>
	<div
		id="item-{ident}"
		class="item"
		contenteditable="true"
		cols="30"
		rows="1"
		value={value}
		placeholder="__"
		on:keyup
		bind:this={self}
	></div>

	{#each children as item,index (item.id)}
		<span>{item.level}</span>
		<Item
			ident={item.id}
			value={item.value}
			parent={item.parent}
			children={item.children}
			style="margin: {item.levels * 16}px"
		></Item>
	{/each}
</div>


<style>
	.item {
		resize: none;
		text-align: left;
	}
</style>